import { Typography, ListItemText, Divider, List, ListItemButton } from "@mui/material";
import React from "react";



function ShoppingListsListItem({item}) {

    function getDate() {
        const date = new Date(item.transaction_date);
        const formattedDate = date.toLocaleDateString();
        return formattedDate
    }

    return (<ListItemButton alignItems="flex-start">
        {/* <ListItemAvatar>
            <Avatar alt="Remy Sharp" src="/static/images/avatar/1.jpg" />
        </ListItemAvatar> */}
        <ListItemText
            primary={item.name}
            secondary={
                <>
                    <Typography
                        sx={{ display: 'inline' }}
                        component="span"
                        variant="body2"
                        color="text.primary"
                    >
                        {item.type}
                    </Typography>
                    {" - " + getDate()}
                </>
            }
        />
    </ListItemButton>);
}

function ShoppingsLists({ lists }) {

    return (
        <List sx={{ width: '100%', bgcolor: 'background.paper' }}>
            {lists.map(item => (
                <React.Fragment key={item.id}>
                    <ShoppingListsListItem item={item} />
                    <Divider component="li" />
                </React.Fragment>
            ))}
        </List>
    );
};

export default ShoppingsLists;